:root {
  --colorTheme: #76e853a4;
}

body, html {
  margin: 0;
  padding: 0;
}

* {
  font-family: Raleway;
}

.hidden {
  display: none;
}

ul, h4, h2, h5 {
  margin: 0;
  padding: 0;
  list-style: none;
}

select, input {
  box-sizing: border-box;
  cursor: pointer;
  width: 100%;
  margin: 5px 0;
  padding: 5px;
}

button {
  background-color: var(--colorTheme);
  color: #404040;
  cursor: pointer;
  border: 1px solid #767676;
  border-radius: 5px;
  margin: 5px 0;
  padding: 6px;
  box-shadow: 0 4px 8px #0003;
}

#topSection {
  flex-direction: row;
  align-items: flex-start;
  width: 100%;
  display: flex;
}

#leftSidebar {
  flex-direction: column;
  display: flex;
}

#headerContainer {
  background-color: var(--colorTheme);
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  display: flex;
}

#changeColorContainer {
  align-items: center;
  display: flex;
}

#changeColorContainer select {
  cursor: pointer;
  border: 1px solid #ccc;
  border-radius: 6px;
  padding: 4px 8px;
}

#newMemberContainer {
  text-align: center;
  background-color: #fff;
  border: 1px solid #d3d3d3;
  border-radius: 10px;
  flex-direction: column;
  margin: 10px;
  padding: 10px;
  display: flex;
  box-shadow: 0 4px 8px #0003;
}

#newMemberForm {
  flex-direction: column;
  display: flex;
}

#teamContainer {
  text-align: center;
  background-color: #f4f8fa;
  border: 1px solid #d3d3d3;
  border-radius: 10px;
  width: 250px;
  margin: 10px;
  padding: 10px;
  box-shadow: 0 4px 8px #0003;
}

#currentTeamContainer {
  text-align: center;
  border-radius: 10px;
  margin: 10px;
  padding: 10px;
}

#memberCardContainer {
  background-color: #ffffff72;
  border: 1px solid #d3d3d3;
  border-radius: 10px;
  margin: 10px;
  padding: 8px;
  list-style-type: none;
  position: relative;
  box-shadow: 0 4px 8px #0003;
}

.deleteIcon {
  width: 12px;
  height: 12px;
}

.deleteIconContainer {
  cursor: pointer;
  position: absolute;
  top: 8px;
  right: 8px;
}

#memberCardContainer:hover {
  transform: scale(1.05);
}

#memberCardContainer span {
  background-color: var(--colorTheme);
  color: #333;
  text-align: center;
  border-radius: 12px;
  width: 50px;
  margin-top: 10px;
  padding: 2px 10px;
  font-size: .75rem;
  display: inline-block;
  box-shadow: 0 4px 8px #0003;
}

#memberCardContainer .deleteIconContainer {
  opacity: 0;
  transition: opacity .2s ease-in-out;
}

#memberCardContainer:hover .deleteIconContainer {
  opacity: 1;
}

#memberCardContainer span:hover {
  background-color: #d9d7d7;
}

#filterSortContainer {
  background-color: #f4f8fa;
  border: 1px solid #d3d3d3;
  border-radius: 10px;
  width: 250px;
  margin: 10px;
  padding: 10px;
  box-shadow: 0 4px 8px #0003;
}

#filterSortContainer h2 {
  text-align: center;
}

#filterSortContainer p {
  text-align: center;
  font-weight: 800;
}

#addTaskContainer {
  background-color: #f4f8fa;
  border: 1px solid #d3d3d3;
  border-radius: 10px;
  justify-content: end;
  margin: 10px;
  padding: 10px;
  box-shadow: 0 4px 8px #0003;
}

#addTaskContainer button {
  width: 15%;
}

#taskBoard {
  flex-direction: column;
  flex-grow: 1;
  display: flex;
}

#taskColumnsContainer {
  justify-content: space-between;
  width: 100%;
  display: flex;
}

.taskColumn {
  text-align: center;
  background-color: #f4f8fa;
  border: 1px solid #d3d3d3;
  border-radius: 10px;
  flex: 1;
  align-items: stretch;
  min-height: 927px;
  margin: 10px;
  padding: 10px;
  box-shadow: 0 4px 8px #0003;
}

.taskColumn h3 {
  font-size: x-large;
}

.taskCard {
  background-color: var(--colorTheme);
  text-align: center;
  border-radius: 12px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: auto;
  min-height: 160px;
  margin: 12px 12px 35px;
  padding: 16px;
  transition-duration: .15s;
  display: flex;
  box-shadow: 0 4px 8px #0003;
}

.taskCard:hover {
  transform: scale(1.05);
}

.taskCard h4 {
  color: #1a1a1a;
  text-align: center;
  margin: 0;
  font-size: larger;
  font-weight: 800;
}

.taskCard p {
  color: #5a5a5a;
  margin: 4px 0;
  font-size: .9rem;
}

.taskCard span {
  color: #333;
  text-align: center;
  text-align: center;
  background-color: #ffffffb7;
  border-radius: 12px;
  min-width: 80px;
  margin: 8px 0;
  padding: 5px 12px;
  font-size: .75rem;
  display: inline-block;
  box-shadow: 0 4px 8px #0003;
}

.taskCard select, .taskCard option, .taskCard button {
  color: #393939;
  text-align: center;
  background-color: #ffffffb7;
  border-radius: 5px;
  width: 50%;
  height: 32px;
  margin-top: 0;
  margin-bottom: 0;
}

.taskCard .deleteIconContainer {
  opacity: 0;
  transition: opacity .2s ease-in-out;
}

.taskCard:hover .deleteIconContainer {
  opacity: 1;
}

select:hover, button:hover {
  background-color: #f1f1f1;
}

#deleteButton {
  background-color: #ffffffb7;
}

#deleteButton:hover {
  color: #fff;
  background-color: #c62828;
}

.taskCard, #memberCardContainer {
  position: relative;
}

#searchTaskContainer {
  background-color: #f4f8fa;
  border: 1px solid #d3d3d3;
  border-radius: 10px;
  flex-direction: column;
  flex: 1;
  justify-content: center;
  margin: 10px;
  padding: 10px 15px;
  display: flex;
  box-shadow: 0 4px 8px #0003;
}

#searchTaskContainer input {
  margin-top: 5px;
}

#searchTaskContainer h2 {
  margin: 0;
  font-size: 1.2rem;
  font-weight: 800;
}

#topTaskBoardContainer {
  gap: 20px;
  display: flex;
}

#addTaskContainer, #searchTaskContainer {
  flex: 1;
  max-width: 100%;
}
